
* { margin:0; padding:0; box-sizing:border-box; }


body {
  /* position: fixed; */
  overflow: hidden;
  width: 100vw;
  margin:0; padding:0;
  box-sizing:border-box;
}

.cityListContainer{
    background: yellow;
    /* height: auto; */
    height: auto;
    width: 100%;
    overflow-x: hidden
}



:root {
    --width: 80px;
    --background: blue
  }


  

.City {
    height: 60px;
    background: green;
    width: 100%;
    /* min-width: 100vw !important; */
    padding: 0px;
    border-bottom: .5px solid black;
    display: flex;
    padding-left: 10px;
    overflow-x: hidden;
}

.CityParentContainer {
display: flex;
/* width: calc(100%+150px); */
width: calc(100% + 80px);
width: calc(100% + var(--width));
transition: -webkit-transform .2s ease-out;
transition: transform .2s ease-out;
transition: transform .2s ease-out, -webkit-transform .2s ease-out;
-webkit-transform: translate(0px);
        transform: translate(0px);
/* pointer-events: none; */


/* transform: translate(0)  */

}

.deleteBtn {

    width: 80px;

    width: var(--width);
    background: red;
    color: white;
    text-align: center;
    padding-top: 20px;
}

.cityContainerLeft {
    height: 100%;
    width: 70%;
    display: flex;
    flex-direction: column
}

.cityTime {
    width: 100%;
    height: 40%;
    font-size: 12px;
    padding-top: 10px;
}

.cityName {
    width: 100%;
    height: 60%;
    font-size: 22px;
    padding-top: 0px;
}

.cityContainerRight {
    height: 100%;
    width: 30%;
    font-size: 38px;
    padding-top: 10px;
    text-align: end;
    padding-right: 20px;
}

/* #city1 {
    transform: translate(-180px)
} */
.UnitBtn {
    font-size: 26px;
} 

.Unit {
    display: flex;
    padding-left: 10px;
    font-size: 14px;
}
